.my-page {
    .title-for-page {
        margin-top: 1rem !important;
    }

    .content {
        padding: 1rem 2rem;

        .d-flex.align-end {
            border-bottom: 1px solid #c4c4c4;
            @media (max-width: 671px) {
                padding-bottom: 0.25rem !important;
            }

            .inp_box_b {
                max-width: 50%;
                min-width: 45%;
                @media (max-width: 671px) {
                    max-width: 100%;
                    width: 100%;
                }

                .ant-select-selection-item {
                    @media (max-width: 671px) {
                        padding: 0px 0px;
                    }
                }
            }
            span.txt {
                color: #474747;
                @media (max-width: 671px) {
                    margin: 0 !important;
                    padding-top: 0.75rem;
                }
            }
        }

        .mb-0 {
            margin: 0 !important;
        }

        .ant-form-item-explain.ant-form-item-explain-error {
            display: none;
        }

        .w-full {
            .inp_box_b {
                min-width: unset !important;
                max-width: unset !important;
                width: 100%;

                label{
                    margin: 0;
                }

                input.hiden.active {
                    color: #000;
                    border: none;
                    padding-left: 0;
                    box-shadow: none;
                }
            }
        }

        .form-change-password {
            border-bottom: 1px solid #c4c4c4;
            margin-top: -0.5rem;    

            .d-flex.align-end {
                border-bottom: none;

                label {
                    margin-bottom: 1.75rem;
                }
            }

            .ant-row.ant-form-item {
                margin: 0 !important;
            }

            .bnt-content-policy {
                justify-content: flex-end !important;
                width: 45%;
                @media (max-width: 671px) {
                    margin: 0 !important;
                    width: 100%;
                }



                #btnLottery {
                    max-width: 250px;
                    padding: 5px 30px;
                    width: max-content;
                    border-radius: 5px;

                    @media (max-width: 671px) {
                        width: max-content;
                    }

                    @media (max-width: 365px) {
                        padding: 10px 22px;
                    }
                }
            }
        }
    }
}
